<template>
	<div id="advertise-list">
		<public-header title="店铺推广记录"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">未开启</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">进行中</div>
					<div class="tab-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">已结束</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<div class="advertise-list" v-if="advertise.length > 0">
				<div class="advertise-item" v-for="(item, index) in advertise" :key="item.id">
					<div class="padding-15 van-hairline--bottom">
						<div class="flex margin-15-b">
							<span class="font-15">{{item.title}} <span class="c-gray font-14 padding-5-l" v-if="item.status == 1">剩余{{item.until}}天</span></span>
						</div>
						<p class="desc" v-if="item.type == 'stick'">置顶排序: No.{{item.displayorder}}</p>
						<p class="desc">广告持续: {{item.days}}天</p>
						<p class="desc">开始时间: {{item.starttime_cn}}</p>
						<p class="desc">结束时间: {{item.endtime_cn}}</p>
						<p class="desc">创建日期: {{item.addtime_cn}}</p>
					</div>
					<div class="status">
						<img src="static/img/activity_ing.png" v-if="item.status == 1" class="img-100" alt="" >
						<img src="static/img/activity_end.png" v-else-if="item.status == 2" class="img-100" alt="" >
						<img src="static/img/activity_not_start.png" v-else-if="item.status == 0" class="img-100" alt="" >
					</div>
				</div>
			</div>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>还没有为店铺推广哦~</p>
			</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				advertise: [],
				isRefresh: false,
				showPreLoading: true,
				filter: {
					items: {
						status: 1,
					}
				}
			}
		},
		methods: {
			onLoad(force) {
				if (force){
					this.advertise = [];
				}
				var that = this;
				getData({
					vue: this,
					url: 'manage/advertise/list/index',
					data: {
						filter: this.filter.items
					},
					success: (res) => {
						that.isRefresh = false;
						that.advertise = [...that.advertise, ...res.advertise];
					}
				});
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
				this.onLoad(true);
			},
			onPullDownRefresh(){
				this.onLoad(true);
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#advertise-list .content .van-pull-refresh{
		margin-top: 45px;
	}
	#advertise-list .content .advertise-item{
		margin: 10px;
		border-radius: 5px;
		overflow: hidden;
		background-color: #fff;
		font-size: 14px;
		position: relative;
	}
	#advertise-list .content .advertise-item .desc{
		color: #7a7a7a;
		font-size: 14px;
		margin-top: 7px;
	}
	#advertise-list .content .advertise-item .status{
		position: absolute;
		top: 0;
		right: 0;
		width: 50px;
		height: 50px;
	}
</style>


